.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
    background-color: white;
}
.popular h2{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.popular hr{
    width: 300px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 10px;
    margin-left: 50px;
    
}
.popular-item img{
    width: 300px;
    height: 200px;
}